@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,400&display=swap');

$light-color-1: #77c4db;
$light-color-2: #9ccfdd;
$light-color-3: #b2d5de;

$dark-color-1: #1f3339;
$dark-color-2: #2a393e;
$dark-color-3: #353e40;

$numElems: 7;

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;

  background-color: $light-color-1;
  background-image: linear-gradient(30deg, $light-color-2 12%, transparent 12.5%, transparent 87%, $light-color-2 87.5%, $light-color-2),
  linear-gradient(150deg, $light-color-2 12%, transparent 12.5%, transparent 87%, $light-color-2 87.5%, $light-color-2),
  linear-gradient(30deg, $light-color-2 12%, transparent 12.5%, transparent 87%, $light-color-2 87.5%, $light-color-2),
  linear-gradient(150deg, $light-color-2 12%, transparent 12.5%, transparent 87%, $light-color-2 87.5%, $light-color-2),
  linear-gradient(60deg, $light-color-3 25%, transparent 25.5%, transparent 75%, $light-color-3 75%, $light-color-3),
  linear-gradient(60deg, $light-color-3 25%, transparent 25.5%, transparent 75%, $light-color-3 75%, $light-color-3);
  background-size: 40px 70px;
  background-position: 0 0, 0 0, 20px 35px, 20px 35px, 0 0, 20px 35px;
}

@function pow($number, $exp) {
  $value: 1;
  @if $exp > 0 {
    @for $i from 1 through $exp {
      $value: $value * $number;
    }
  } @else if $exp < 0 {
    @for $i from 1 through -$exp {
      $value: $value / $number;
    }
  }
  @return $value;
}

@function fact($number) {
  $value: 1;
  @if $number > 0 {
    @for $i from 1 through $number {
      $value: $value * $i;
    }
  }
  @return $value;
}

@function pi() {
  @return 3.14159265359;
}

@function rad($angle) {
  $unit: unit($angle);
  $unitless: $angle / ($angle * 0 + 1);
  // If the angle has 'deg' as unit, convert to radians.
  @if $unit == deg {
    $unitless: $unitless / 180 * pi();
  }
  @return $unitless;
}

@function sin($angle) {
  $sin: 0;
  $angle: rad($angle);
  // Iterate a bunch of times.
  @for $i from 0 through 10 {
    $sin: $sin + pow(-1, $i) * pow($angle, (2 * $i + 1)) / fact(2 * $i + 1);
  }
  @return $sin;
}

@function cos($angle) {
  $cos: 0;
  $angle: rad($angle);
  // Iterate a bunch of times.
  @for $i from 0 through 10 {
    $cos: $cos + pow(-1, $i) * pow($angle, 2 * $i) / fact(2 * $i);
  }
  @return $cos;
}

.app {
  width: 100vw;
  height: 100vh;

  overflow: hidden;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.title {
  margin: 10px;
  font-size: 1.75em;
  font-weight: bold;
  background-color: white;
  padding: 15px;
  border-radius: 15px;
  box-sizing: border-box;
  text-align: center;
  width: 40%;
  max-width: 450px;
  min-width: 350px;
}

.description {
  font-size: 0.85em;
  margin: 10px;
  width: 40%;
  max-width: 450px;
  min-width: 350px;
  height: 250px;
  min-height: 250px;

  background-color: white;

  border-radius: 20px;
  box-sizing: border-box;
  padding: 15px;

  transition: 0.2s;
  display: flex;
  flex-direction: column;

  &.hide {
    opacity: 0;
  }

  .header {
    padding-left: 15px;
    font-weight: bold;
    font-size: 1.5em;
    border: none;
    border-bottom: 1px solid black;
  }

  .body {
    height: 100%;

    &.centered {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &.list {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
    }

    &.text {
      padding-top: 10px;
      text-align: justify;
      line-height: 1.25;
    }

    .horz-list {
      display: flex;
      width: 100%;
      margin-top: 20px;
      justify-content: space-evenly;
    }

    .vert-list {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      height: 100%;
      align-items: center;

      span {
        font-size: 1.25em;
      }
    }

    a {
      font-size: 1.5em;
      text-decoration: none;
      text-align: center;
      font-weight: bold;
      color: black;
      transition: 0.2s;

      background-color: $light-color-3;
      padding: 0.25em;
      border-radius: 0.25em;
      box-shadow: 0 0.2em 0 0 $light-color-1;

      &:only-child {
        font-size: 2em;
      }

      &:hover {
        background-color: $light-color-3;
        transform: translate(0,0.1em);
        box-shadow: 0 0.1em 0 0 $light-color-2;
      }
    }
  }

  .education-item, .experience-item {
    display: flex;
    flex-direction: column;

    .secondary-row {
      display: flex;
      padding-left: 5px;
      font-size: 0.9em;
      width: 100%;

      .date {
        margin-right: 10px;
      }
    }

    .name {
      font-weight: bold;
    }
  }
}

.container {
  width: 350px;
  height: 350px;
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;


  .item-container {
    position: absolute;
    z-index: 2;

    &:hover, &.force-hover {
      .item {
        background-color: #61beef;
      }
    }
  }

  .circle {
    transition: 0.2s;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 50%;

    background-color: white;

    width: 1.6em;
    height: 1.6em;
  }

  .face {
    z-index: 5;
    transition: 0.2s;
    height: 50%;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &:hover, &.force-hover {
    .face {
      transform: scale(1.1);
    }
  }

  @for $i from 2 through ($numElems + 1) {
    :nth-child(#{$i}) {
      .item {
        transition-delay: 0.05s * ($i - 1);
      }
    }
  }
}

@media screen and (min-height: 600px) {
  .container {

    .circle {
      width: 60px;
      height: 60px;
    }

    .item-container {
      font-size: 35px;
    }

    &:hover, &.force-hover {
      @for $i from 2 through ($numElems + 1) {
        :nth-child(#{$i}) {
          $degPerElem: 360deg / $numElems;
          $angle: (($i - 2) * $degPerElem)+90deg;
          $radi: -120px;
          $xval: cos($angle)*$radi;
          $yval: sin($angle)*$radi;

          .item {
            transform: translate($xval, $yval);
          }
        }
      }
    }
  }
}

@media screen and (max-height: 600px) {

  .item-container {
    font-size: 4vh;
  }

  .container {
    &:hover, &.force-hover {
      @for $i from 2 through ($numElems + 1) {
        :nth-child(#{$i}) {
          $degPerElem: 360deg / $numElems;
          $angle: (($i - 2) * $degPerElem)+90deg;
          $radi: -16vh;
          $xval: cos($angle)*$radi;
          $yval: sin($angle)*$radi;

          .item {
            transform: translate($xval, $yval);
          }
        }
      }
    }
  }
}
